<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-start">
                    <div class="d-flex flex-column">
                        <h4 class="modal-title mb-1">{{ $t('TransactionHistory') }}</h4>
                        <!-- <h6 class="modal-subtitle">
                        {{ `${$t('Alltransactionsfor')} ${(RecordData && RecordData.CUSTOMER_NAME) || '--'} (${(RecordData && RecordData.ACCOUNT_NO) || '--'})` }}
                        </h6> -->
                    </div>
                    <button type="button" class="close" @click="close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                    </div>

                    <div class="modal-body">

                            <div class="col-lg-12 mt-0 mb-0 p-0">
                                <div class="heading-border mt-2">
                                    <h5 class="h5"><span>{{ `${$t('Alltransactionsfor')} ${(RecordData && RecordData.CUSTOMER_NAME) || '--'} (${(RecordData && RecordData.ACCOUNT_NO) || '--'})` }}</span></h5>
                                </div>
                            </div>

                        <tg-list :IsShowAction="false" ModuleName="Company" SubModuleCode="" IdentityColumn="COMPANY_ID" :listType="['List']" :ListData="HistoryData"
                         :HeaderData="Headers" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchRecords" :showCheckBox="false"  v-on:PagerButtonClick="pagerMethod"
                         :NorecordfoundText="$t('NoRecordfound')">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                <td class="text-center single-action tourguide_a" v-if="data.row.CREATED_AT">
                                    {{ $options.filters.formatDateTime(toUserDateTime(data.row.CREATED_AT,data.row.TIMEZONE)) }}
                                    <!-- {{ $options.filters.formatDateTime(data.row.CREATED_AT) }} -->
                                </td>
                                <td class="text-center single-action tourguide_a" v-else>--</td>
                            </template>
                            <template class="text-right" v-if="data.column.COLUMN_NAME == 'AMOUNT'">
                                <span :class="['text-right single-action tourguide_a', data.row.TXN_TYPE == 'CREDIT' ? 'text-success' :'text-danger']" v-if="data.row.AMOUNT">
                                {{ (data.row.TXN_TYPE == 'CREDIT' ? '+' : '-') +
                                    $n(data.row.AMOUNT,'currency',$getLocaleByCurrency(Currency))
                                    }}
                                </span>
                                <td class="text-center single-action tourguide_a" v-else>0</td>
                            </template>
                            <template class="text-right" v-if="data.column.COLUMN_NAME == 'AFTER_BALANCE'">
                                <span class="text-right single-action tourguide_a" v-if="data.row.AFTER_BALANCE">
                                    {{ $n(data.row.AFTER_BALANCE,'currency',$getLocaleByCurrency(Currency)) }}
                                </span>
                                <td class="text-center single-action tourguide_a" v-else>0</td>
                            </template> 
                        </template>
                  </tg-list>
                    </div>   
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';

export default{
       props:{
        RecordData:{
            type:Array,
            required:true,
        }     
        },
    data(){
        return{
            Currency:'',
            isLoading:false,
            client_id:0,
            PageNumber: 1,
            PageSize: 10,
            HistoryData:[],
            TotalRecords:0,
            Headers: [
            {
                "COLUMN_NAME": "TRANSACTION_REFERENCE_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Transaction Reference No.",
                "DISPLAY_ORDER": 6,
            },    
            {
                "COLUMN_NAME": "TXN_TYPE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Transaction Type"),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("DateTime"),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true
                }                
            },
         
            {
                "COLUMN_NAME": "AMOUNT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Amount"),
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "AFTER_BALANCE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Balance"),
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            }
            
            ],
       }
    },
    created:function(){      
        var vm=this;
        vm.Currency=vm.GetUserInfo.Currency.replace(/[()]/g, '');
        if(vm.RecordData != '')
         {
            vm.client_id=vm.RecordData.CLIENT_ID;
             vm.FetchRecords();
         }
    },
    methods:{
        close(){
            this.$parent.IsHistory=false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        FetchRecords() {
        debugger;
        this.isLoading = true;
        const url = `pageSize=${this.PageSize}&pageNum=${this.PageNumber}&client_id=${ this.client_id}&type=ALL`;
        DataService.GetCompanyWalletHistory(url)
        .then((response) => {
            debugger
            const json_data = JSON.parse(response?.data[0].JSON_RESULT) || [];
            const data=json_data.JSON_HISTORY;
            if (data.length > 0) {
                this.HistoryData = data.map(item => ({
                    ...item,
                    CREATED_AT: item.CREATED_AT ? new Date(item.CREATED_AT) : null
                }));

                this.TotalRecords = data[0].TOTAL_RECORDS;
            } else {
                this.HistoryData = [];
                this.TotalRecords = 0;
            }
            this.isLoading = false;
        })
}
    }
}
</script>
