<template>
    <div class="my-popups">
       <loader :is-visible="isLoading"></loader>
       <div class="modal d-block">
           <div class="modal-dialog modal-dialog-centered">
               <div class="modal-content">
                   <div class="modal-header theme-primary partition-full  d-flex">
                       <h4 class="modal-title">Manual Wallet Adjustment</h4>

                       <button type="button" class="close" @click="Close">
                           <em class="fa fa-times" aria-hidden="true"></em>
                       </button>                        
                   </div>
                   <div class="modal-body">
                       <dynamic-form lang="en" :buttons="buttons" :schema="FromSchema" ref="AddRecharge"  :mandatorytext='MandatoryString'></dynamic-form>
                   </div>   
               </div>
           </div>
       </div>
   </div>
   </template>

<script>
import DataService from '../../../services/DataService';
export default{
   props:{
    Adjustdata:{
           type:Array
       }
   },
   data(){
       return{
           MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
          FromSchema:[
       {
           layoutType: "Single",
           Data: [
               {
                   astype: "TextField",                                                
                   label: 'Current Balance',
                   name: "CurrentBalance",
                   value: "0.00",                            
                   validationRules: { "required": true, max: 100,},
                   disabled: true,
               },
               {
                   astype: "RadioField",
                   label: "AdjustmentType",
                   name: "AdjustmentType",
                   value: 'AddCredit',
                   placeholder: "",
                   disabled: false,
                   chktxtIndex: 0,
                   config: {
                       options: [
                           { value: "AddCredit", name: "Add Credit" },
                           { value: "DeductAmount", name: "Deduct Amount" }
                       ],                           
                   },
                   validationRules: "required"
               },    
               {
                   astype: "NumericField",                                                
                   label: 'Amount',
                   name: "Amount",
                   value: "0.00",                                                      
                   validationRules: { "required": true, max: 100 },
               },
               {
                   astype: "TextAreaField",
                   label:"Reason",
                   disabled: false,
                   name: "Reason",
                   value: "",
                   placeholder:"Reason",
                   value: "",  
                   validationRules: { "required": true, max: 4000 },
                   config: {
                       rows:3
                   }
               }    
                                 
           ]
       }
   ],
   buttons: [
               {
                   type: "submit",
                   class: "btn btn-success",
                   text: "<i class='fa fa-save pr-2'></i>Save",
                   isVisible:true
               },            
               {
                   type: "button",
                   class: "btn btn-danger",
                   text: "<i class='fa fa-close pr-2'></i>Cancel",
                   onClick: this.Close,
               },
           ],

       }
   },
   created(){
    debugger
       if(this.Adjustdata)
       {
        this.FromSchema[0].Data[0].value=this.Adjustdata.CURRENT_BALANCE;
       }
   },
   methods:{
   Close(){
       this.$parent.IsAdjustBalance=false;
   },
   }
}

</script>