<template>
     <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-start">
                        <div class="d-flex flex-column">
                        <h4 class="modal-title mb-1">{{ $t('AddOfflineRecharge') }}</h4>
                        <!-- <h6 class="modal-subtitle mt-1">
                        {{ `${(RechargeData && RechargeData.CUSTOMER_NAME) || '--'} (${(RechargeData && RechargeData.ACCOUNT_NO) || '--'})` }}
                        </h6> -->
                    </div>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <div class="col-lg-12 mt-0 mb-3 p-0">
                                <div class="heading-border mt-2">
                                    <h5 class="h5"><span>{{ `${(RechargeData && RechargeData.CUSTOMER_NAME) || '--'} (${(RechargeData && RechargeData.ACCOUNT_NO) || '--'})` }}</span></h5>
                                </div>
                            </div>
                        <dynamic-form lang="en" :buttons="buttons" :schema="FromSchema" ref="AddRecharge" v-on:OnSubmit="AddPaymentTranaction" :mandatorytext='MandatoryString'></dynamic-form>
                    </div>   
                </div>
            </div>
        </div>
    </div>
    </template>

<script>
import DataService from '../../../services/DataService';
 export default{
    props:{
        RechargeData:{
            type:Array
        }
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
           FromSchema:[
        {
            layoutType: "Single",
            Data: [
                {
                    astype: "TextField",                                                
                    label: 'Current Balance',
                    name: "CurrentBalance",
                    value: "0.00",                            
                    validationRules: { "required": true, max: 100,},
                    disabled: true,
                },
                {
                    astype: "NumericField",                                                
                    label: 'Recharge Amount',
                    name: "RechargeAmount",
                    placeholder: "0.00",                                                      
                    validationRules: { "required": true, max: 10 },
                    
                },
                {
                    astype: "RadioField",
                    label: "PaymentType",
                    name: "PaymentType",
                    value: 'Cash',
                    placeholder: "",
                    disabled: false,
                    chktxtIndex: 0,
                    config: {
                        options: [
                            { value: "Cash", name: "Cash" },
                            { value: "Check", name: "Check" },
                            { value: "MoneyOrder", name: "Money Order" }
                        ],                           
                    },
                    validationRules: "required"
                },                      
            ]
        },
        {
            layoutType: "double",
            Data: [
                {
                    astype: "DateField",
                    label: "Date",
                    disabled: false,
                    name: "Date",
                    value: new Date(),
                    placeholder:"Date",
                    validationRules: {
                        required: true,
                    },
                    config: { mode: "date", format: "M/D/YYYY", 
                    minDate:'', maxDate: new Date(),
                    },
                }, 
                {
                    astype: "TextField",                                                
                    label: 'Reference',
                    name: "Reference",
                    value: "",                                                      
                    validationRules: { "required": true, max: 6 },
                    
                },                
            ]
        },
        {
            layoutType: "single",
            Data: [
                {
                    astype: "TextAreaField",
                    label:"Remark",
                    disabled: false,
                    name: "Remark",
                    value: "",
                    placeholder:"Remark",
                    value: "",  
                    validationRules: { "required": true, max: 1000 },
                    config: {
                        rows:3
                    }
                }              
            ]
        },
    ],
    buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true
                },            
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],

        }
    },
    created:function(){
        debugger
       this.isLoading=true;
       if(this.RechargeData)
       {
        this.FromSchema[0].Data[0].value=this.RechargeData.CURRENT_BALANCE;
       }
       this.isLoading=false;
    },
    methods:{

         async AddPaymentTranaction() {
                    debugger
                    var vm = this;
                    const url = `Amount=${vm.FromSchema[0].Data[1].value}&Description=WALLET_RECHARGE&Source=Admin`;
                    try {
                        const response = await DataService.AddPaymentTranaction(url);

                        if (response && response.data) {

                            vm.UniqueId = response.data[0].UniqueId;
                            vm.TransactionId = response.data[0].TransactionId;
                            // Call AddRechargeSubmit after receiving transaction info
                            await vm.AddRechargeSubmit();
                        } else {
                            vm.UniqueId = null;
                            vm.TransactionId = null;
                        }
                    } catch (err) {
                        console.error('AddPaymentTranaction error:', err);
                    }
                },
        async AddRechargeSubmit(){
        debugger
       var vm=this;
       const isValid = await vm.$refs.AddRecharge.validateForm.validate();
     if(isValid && vm.FromSchema[0].Data[1].value > 0)
     {
        debugger
        vm.isLoading=true;
        vm.$parent.CustomerData.CURRENT_BALANCE=vm.$parent.CustomerData.CURRENT_BALANCE + Number(vm.FromSchema[0].Data[1].value);
      var Client_id= this.RechargeData.CLIENT_ID;
       var obj={ 
         client_id:Client_id,
        rechargeAmount:vm.FromSchema[0].Data[1].value,
        recharge_Mode:vm.FromSchema[0].Data[2].value,
        recharge_date:vm.FromSchema[1].Data[0].value,
        recharge_instrument_no :vm.FromSchema[1].Data[1].value,
        recharge_remark:vm.FromSchema[2].Data[0].value,
        action_name:'CREDIT',
        bank_name:'SBI',
        instrument_no:'',
        reference_no: vm.TransactionId,
        reference_type:'TALYGEN_bill_payment_transaction'
       };
       var data=JSON.stringify(obj);
      await  DataService.AddRecharge(data).then((response)=>{
                if(response.data[0].Result=="Insufficient_balance")
                {
                    vm.ShowAlert(vm.$t("Insufficientbalance"), "failure", vm.$t('Alert'));
                }
                else if(response.data[0].Result=="Invalid_amount")
                {
                    vm.ShowAlert(vm.$t("Invalidamount"), "failure", vm.$t('Alert'));
                }
                else if(response.data[0].Result=="Update")
                {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Recharge')]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$emit('refreshlist');
                }
                else{
                    vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                }
                vm.isLoading=false;
        });
     }
     else{
        {
        vm.ShowAlert(vm.$t('MandatoryString'), "failure", true, vm.$t("Alert"))
      }
     }
    },
    Close(){
        this.$parent.IsRecharge=false;
    },
    }
 }

</script>