<script>
import AddRecharge from './AddRecharge.vue';
import AdjustBalance from './AdjustBalance.vue';
import ViewHistory from './ViewHistory.vue';
import DataService from '../../../services/DataService';
export default{
  components:{AddRecharge,AdjustBalance,ViewHistory},
    props:{
        CustomerData:{
            type:Array
        },
    },
    data(){
          return{
            isLoading:false,
             IsRecharge:false,
             IsHistory:false,
             IsAdjust:false,
             Currency:'',
             RecentRecords:[],
             WidgitData:[],
             LastUpdatedDate:'',
             StatusName:'',
             TimeZone:''
          }
        },
       async created(){  
         this.Currency=this.GetUserInfo.Currency.replace(/[()]/g, '');
        await  this.FetchRecords(this.CustomerData.CLIENT_ID);
        await this.FetchWidgetDetail();
        },
    methods:{
      RefreshListing(){
        debugger;
        this.FetchRecords(this.CustomerData.CLIENT_ID);
        this.FetchWidgetDetail();
      },
     async FetchWidgetDetail(){
          var vm=this;
          vm.isLoading=true
          var url=`CompanyId=${vm.GetUserInfo.CompanyId}&ClientId=${this.CustomerData.CLIENT_ID}`
       await DataService.GetCompanyWalletData(url).then((response)=>{
            if(response.data.result !='')
            {
                vm.WalletData=response.data.result.Overall[0];
                 vm.percentageData=response.data.result.MonthComparison[0];
                 vm.utilityConsumptionData=response.data.result.UtilityConsumption[0];
                vm.WidgitData=response.data.result.CONSUMPTION_WIDGET[0];
            }
            else{
                vm.WalletData=[];
                vm.percentageData=[];
                vm.utilityConsumptionData=[];
            }
             this.isLoading=false;
          })
        },
     async FetchRecords(client_id) {
        this.isLoading = true;
        // debugger;
        const url = `client_id=${client_id}&type=RECENT`;
      await  DataService.GetCompanyWalletHistory(url)
        .then((response) => {
           debugger;
          const json_data = JSON.parse(response?.data[0].JSON_RESULT) || [];
          const data=json_data.JSON_HISTORY;
            if (data.length > 0) {
              debugger;
                this.RecentRecords = data.map(item => ({
                    ...item,
                    CREATED_AT: item.CREATED_AT ? new Date(item.CREATED_AT) : null
                }));
                this.TotalRecords = data.length;
               this.LastUpdatedDate = this.RecentRecords[0].MODIFIED_AT;
               this.StatusName = this.RecentRecords[0].STATUS_NAME;
               this.TimeZone = this.RecentRecords[0].TIMEZONE;
               
            } else {
                this.RecentRecords = [];
                this.TotalRecords = 0;
                this.LastUpdatedDate = '';
                this.StatusName = '';
                this.TimeZone = '';
            }
            this.isLoading=false;
        })
       },
      OpenRechargeModel(){
        debugger
         this.RechargeData=this.CustomerData;
         this.IsRecharge=true;
      },
      OpenAdjustBalanceModel(){
        debugger
        this.Adjustdata=this.CustomerData;
        this.IsAdjust=true;
      },
      OpenHistoryModel(){
        debugger
        this.RecordData=this.CustomerData;
        this.IsHistory=true;
      },
        Close(){
            this.$parent.IsCustomerData=false;
        },
    }
 }
</script>
