<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">

                        <div class="modal-header d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">Enable Wallet For Clients</h5>
                            <button type="button" class="close" @click="Close()">
                                <em class="fa fa-times"></em>
                            </button>
                        </div>

                        <div class="modal-body">

                            <!--  Search Filter -->
                            <input 
                                type="text"
                                class="form-control mb-3"
                                placeholder="Search by name, account number, or email"
                                v-model="searchQuery"
                            />

                            <!--  Table -->
                            <div class="listing">
                                <div class="table-responsive table-fix-header" id="div_forListView">
                                    <table class="table table-bordered mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="width: 50px;">

                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input type="checkbox"  @change="toggleSelectAll"
                                                            :checked="isAllSelected" class="chkItems custom-control-input" id="chk_" />
                                                        <label class="custom-control-label" for="chk_"></label>
                                                    </div>

                                                    <!-- <input 
                                                        type="checkbox" 
                                                        @change="toggleSelectAll"
                                                        :checked="isAllSelected"
                                                    /> -->
                                                </th>
                                                
                                                <th>{{ $t("ClientName") }}</th>
                                                <th>{{ $t("AccountNo") }}</th>
                                                <th>{{ $t("Email") }}</th>
                                                <th>{{ $t("CreatedAt") }}</th>
                                                <th>{{ $t("Status") }}</th>
                                            </tr>
                                        </thead>

                                        <tbody>
                                            <tr v-for="client in filteredClients" :key="client.user_id">
                                                <td>
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input type="checkbox" :value="client.user_id"
                                                            v-model="selectedClientIds"
                                                            class="chkItems custom-control-input"
                                                            :id="'chk_' + client.user_id" />
                                                        <label class="custom-control-label"
                                                            :for="'chk_' + client.user_id"></label>
                                                    </div>

                                                    <!-- <input  
                                                        type="checkbox"
                                                        :value="client.user_id"
                                                        v-model="selectedClientIds"
                                                    /> -->
                                                </td>
                                                <td>{{ client.name }}</td>
                                                <td>{{ client.account_no }}</td>
                                                <td>{{ client.email_id }}</td>
                                                <td>{{ $options.filters.formatDateTime(new Date(client.created_at))}}</td>
                                                <td> <span v-bind:class="getStatusClassList(client.status_name, 'STATUS_NAME')">{{ client.status_name }}</span></td>
                                            </tr>

                                            <tr v-if="filteredClients.length === 0">
                                                <td colspan="3" class="text-center text-muted">
                                                    No clients found.
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                          

                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="AddEnableClients()"
                                title="Save"><i class="fa fa-floppy-o pr-2"></i>Save</a>
                            <a href="javascript:;" class="btn btn-danger delete-btn" v-on:click="Close()"
                                title="Cancel"><i class="fa fa-times pr-2"></i>Cancel</a>
                        </div>
                        <!-- <div class="modal-footer d-flex justify-content-end">
                            <button type="button" class="btn btn-secondary mr-2" @click="Close()">Cancel</button>
                            <button type="button" class="btn btn-primary"
                                :disabled="selectedClientIds.length === 0"
                                @click="AddEnableClients()">
                                Save
                            </button>
                        </div> -->

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from "../../services/DataService";

export default {
    props: {
        ClientData: {
            type: Array,
            default: () => []
        }
    },

    data() {
        return {
            isLoading: false,
            selectedClientIds: [],
            searchQuery: ""
        };
    },

    computed: {
        filteredClients() {
            const q = this.searchQuery.toLowerCase();
            return this.ClientData.filter(c =>
                c.name.toLowerCase().includes(q) ||
                (c.account_no + "").includes(q) ||
                (c.email_id + "").toLowerCase().includes(q)
            );
        },

        isAllSelected() {
            return (
                this.filteredClients.length > 0 &&
                this.filteredClients.every(c =>
                    this.selectedClientIds.includes(c.user_id)
                )
            );
        }
    },

    methods: {
        Close() {
            this.$parent.IsClientData = false;
        },

        toggleSelectAll(event) {
            if (event.target.checked) {
                // Add all filtered client IDs
                const ids = this.filteredClients.map(c => c.user_id);
                this.selectedClientIds = Array.from(new Set([...this.selectedClientIds, ...ids]));
            } else {
                // Remove only filtered client IDs
                this.selectedClientIds = this.selectedClientIds.filter(
                    id => !this.filteredClients.some(c => c.user_id === id)
                );
            }
        },

        AddEnableClients() {
            const vm = this;

            if (!Array.isArray(vm.selectedClientIds) || vm.selectedClientIds.length === 0) {
                vm.$parent.ShowAlert("No clients selected.", "error");
                return;
            }

            const payload = { client_ids: vm.selectedClientIds };
            const payloadJson = JSON.stringify(payload);

            vm.isLoading = true;

            DataService.EnableClientsForRecharge(payloadJson)
                .then(response => {
                    if (response.data && response.data.status === "Success") {
                        vm.$parent.ShowAlert("Clients enabled successfully!", "success");
                        vm.$parent.RefreshListing();
                        vm.Close();
                    } else {
                        vm.$parent.ShowAlert("Failed to enable clients.", "error");
                    }
                })
                .catch(error => {
                    vm.$parent.ShowAlert("An error occurred: " + error, "error");
                })
                .finally(() => {
                    vm.isLoading = false;
                });
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Published':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;


                default:
                    returnClass = '';
            }
            return returnClass;
        }
    }
};
</script>
<style scoped>
.table-hover tbody tr:hover {
    background: #f8f9fa;
}
</style>