       <!-- Seacrh Section  -->

        <div class="heading-border heading-cust pt-2 mb-3">
           <h5 class="h5">
             <span>Customer Wallets</span>
            </h5>
        </div>


        <div class="space-y-4">
          <div>
              <p class="text-muted-foreground">Search, recharge, and manage customer accounts</p>
          </div>
          <section class="mt-2">
              <div class="search-box mt-3">
                <input type="text"
                class="form-control form-control-lg"
                placeholder="Search by name or account number..."
                v-model="SearchData"
                @input="FetchWalletListing"
              />
              </div>
          </section>
          
        </div>
        <!-- Ltsting Section  -->
        <section>
            <tg-list :IsShowAction="true" ModuleName="Company" SubModuleCode="" :LegendArray="[]" 
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="COMPANY_ID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchWalletListing" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'CUSTOMER_NAME'">
                        <a href="javascript:;" v-on:click="OpenCustomerDetail(data.row)"><i aria-hidden="true"></i>
                          {{data.row.CUSTOMER_NAME }}
                        </a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                        <div>
                            <listing-status :isfor="'COMPANY_WALLET'" ref="updatestatus"
                                v-bind:dataid="data.row.WALLET_ID" data-controller="Billing_Rule"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.row.STATUS_ID"
                                data-action="index" v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                                v-bind:callbackfunction="RefreshListing" v-bind:isselectedvaluestring="true" v-bind:title="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'">
                            </listing-status>
                        </div>
                        <div>
                            <span v-bind:class="getStatusClassList(data.row.STATUS_ID, 'STATUS_ID')"
                                v-if="data.row.STATUS_ID == 'Active'">
                                {{ data.row.STATUS_ID }}
                            </span>
                            <span v-bind:class="getStatusClassList(data.row.STATUS_ID, 'STATUS_ID')"
                                v-if="data.row.STATUS_ID == 'Inactive'">
                                {{ data.row.STATUS_ID }}
                            </span>
                        </div>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                              <td class="text-center single-action tourguide_a" v-if="data.row.CREATED_AT">
                                  {{    $d(data.row.CREATED_AT) }}
                              </td>
                              <td class="text-center single-action tourguide_a" v-else>--</td>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'CURRENT_BALANCE'">
                             <span>{{ $n(data.row.CURRENT_BALANCE,'currency',$getLocaleByCurrency(Currency)) }}</span>
                    </template>
                </template>
            </tg-list>
          </section>
        <div v-if="IsRecharge"><AddRecharge :RechargeData="RechargeData" @refreshlist="RefreshListing"/></div>
        <div v-if="IsHistory"><RecordsHistory :RecordData="HistroyData"/></div>
        <!-- <div v-if="IsAdjustBalance"><AdjustBalance :Adjustdata="Adjustdata"/></div> -->
        <div v-if="IsCustomerData"><CustomerDetail :CustomerData="CustomerData"/></div>

        <CompanyClientData v-if="IsClientData" :ClientData="ClientData"></CompanyClientData>
    </div>
  </div>
</template>

<script>
import RecordsHistory from './ViewHistory.vue';
import AddRecharge from './AddRecharge.vue';
// import AdjustBalance from './AdjustBalance.vue';
import DataService from '../../../services/DataService.js';
import Schema from './WalletSchema.js';
import CustomerDetail from './CustomerDetail.vue';
import CompanyClientData from '../../../components/Modals/CompanyClientData.vue';
export default{
    components:{
      RecordsHistory,
      AddRecharge,
      // AdjustBalance,
      CustomerDetail,
    CompanyClientData},
   data(){
    return{
        isLoading:false,
        IsRecharge:false,
        IsClientData: false,
        RechargeData:[],
        HistroyData:[],
        IsHistory:false,
        // IsAdjustBalance:false,
        // Adjustdata:[],
        TotalRecords: 0,
        noRecord: false,
        PageNumber: 1,
        PageSize: 10,
        SortBy: '',
        SortExp: '',
        SearchData:'',
        Currency:'',
        avarageData:'',
        listActions:  [
          {
              title: 'AddRecharge',
              callbackfunction: 'AddRecharge',
              icon: 'fa fa-bolt text-success',
              additionalSpan: '',
              isDisabled: false,
              isvisible: true,
          },
          // {
          //     title: 'AdjustBalance',
          //     callbackfunction: 'AdjustBalance',
          //     icon: 'fa fa-money text-success',
          //     additionalSpan: '',
          //     isDisabled: false,
          //     isvisible: true,
          // },
          {
              title: 'ViewHistory',
              callbackfunction: 'ViewHistory',
              href: 'javascript:void(0)',
              icon: 'fa fa-eye text-primary',
              additionalSpan: '',
              isDisabled: false,
              isvisible: true,
          }
        ],
        WalletData:[],
        percentageData:[],
        StatusListForListing: [
                {
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
        ],
        ListingData:[],
        Headers: [{
                "COLUMN_NAME": "CUSTOMER_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Customer"),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true
                }
                
            },
            {
                "COLUMN_NAME": "ACCOUNT_NO",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("AccountNumber"),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "CURRENT_BALANCE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Balance"),
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "CREATED_BY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CreatedBy'),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CreatedAt'),
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Status"),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }
            }
         
          ],
          IsCustomerData:false,
          CustomerData:[],
          CompanyClientCount: 0,
          utilityConsumptionData:[],
          ClientData:[],      
          MinimumWalletBalance: 0
    }
   },
created :function(){
      this.Currency=this.GetUserInfo.Currency.replace(/[()]/g, '');
      this.FetchWidgetDetail();
      this.FetchWalletListing();
      this.GetCompanyClient();
       this.GetClientMinimumWalletBalance();
},

methods:{
   OpenClientDetails() {
    this.IsClientData = true
  },
  async GetCompanyClient() {
              debugger
              let response = await DataService.GetCompanyClient();
              if (response.data) {
                  this.ClientData = response.data.Data;
                  this.CompanyClientCount = this.ClientData.length;
                 
              }
          },
   async GetClientMinimumWalletBalance(){    
                var vm =this;
                vm.MinimumWalletBalance='';
                var params='key=MinimumWalletBalance '
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                    vm.MinimumWalletBalance =response.data;
                });
            },
          RefreshListing(){
            debugger
            this.FetchWidgetDetail();
            this.FetchWalletListing();
            this.GetCompanyClient();
          },
         getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchWidgetDetail(){
          var vm=this;   
          vm.isLoading=true
          var url=`CompanyId=${vm.GetUserInfo.CompanyId}&ClientId=0`
          DataService.GetCompanyWalletData(url).then((response)=>{
            if(response.data.result !='')
            {
                vm.WalletData=response.data.result.Overall[0];
                vm.percentageData=response.data.result.MonthComparison[0];
                vm.utilityConsumptionData=response.data.result.UtilityConsumption[0];
                vm.avarageData=response.data.result.PER_CUSTOMER_AVG[0];
            }
            else{
                vm.WalletData=[];
                vm.percentageData=[];
                vm.utilityConsumptionData=[];
                vm.avarageData=[];
            }
            vm.isLoading=false;
          })
        },
        FetchWalletListing(){
          var vueObj=this;   
          // vueObj.isLoading=true;
          var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&searchByText=${vueObj.SearchData}`;
          DataService.GetCompanyWalletList(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.length > 0) {
                            vueObj.ListingData = response.data;
                            vueObj.ListingData.forEach((element) => {
                                ['CREATED_AT', 'MODIFIED_AT'].forEach((key) => {
                                    if (element[key]) {
                                        element[key] = new Date(element[key]);
                                    }
                                });
                            });
                            vueObj.TotalRecords = response.data[0].TOTAL_RECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                            // vueObj.isLoading = false;
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.ListingData = [];
                            vueObj.TotalRecords = 0;
                            // vueObj.isLoading = false;
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ListingData = [];
                        vueObj.TotalRecords = 0;
                        // vueObj.isLoading = false;
                    }
          })
         
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchWalletListing();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchWalletListing();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case "AddRecharge":
                    this.OpenRechargeModal(item);
                    break;
                // case "AdjustBalance":
                //   this.OpenAdjustBalanceModal(item);
                //     break;
                case "ViewHistory":
                     this.OpenHistoryModal(item);
                    break;
            }
        },
        OpenRechargeModal(data){
          this.RechargeData=data;
           this.IsRecharge=true;
        },
        OpenHistoryModal(data){
           this.HistroyData=data;
           this.IsHistory=true;
        },
        // OpenAdjustBalanceModal(data){
        //      this.Adjustdata=data;
        //      this.IsAdjustBalance=true
        // }, 
        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        OpenCustomerDetail(row){
          debugger;
          this.CustomerData = row;
          this.IsCustomerData=true
        }
        
        
        
   }
}

</script>




